.cart {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  padding: 50px 0 19px 0;

  @media (max-width: $mobile) {
    padding: 40px 15px 24px 15px;
  }
}

.cart h2 {
  margin: 0;
  margin-bottom: 38px;

  font-family: $font-family-ui;
  font-weight: bold;
  font-size: 26px;
  line-height: 130%;
  text-align: center;
  color: $primary;

  @media (max-width: $mobile) {
    margin-bottom: 30px;
    max-width: 190px;

    font-size: 24px;
    line-height: 120%;
  }
}

.cart__info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
  padding-left: 37px;
  padding-right: 30px;

  width: 100%;

  @media (max-width: $mobile) {
    padding: 0;
    margin-bottom: 30px;
  }
}

.cart__product {
  display: flex;
}

.cart__image {
  width: 84px;
  height: 79px;
  margin-top: 7px;
  margin-right: 20px;

  @media (max-width: $mobile) {
    width: 70px;
    height: 63px;
    margin-right: 15px;
    margin-top: 3px;
  }
}

.cart__image img {
  object-fit: cover;
  max-height: 100%;
}

.cart__product-info h3 {
  margin: 0;
  margin-bottom: 10px;

  font-family: $font-family-ui;
  font-weight: bold;
  font-size: 18px;
  line-height: 130%;
  color: $primary;

  @media (max-width: $mobile) {
    font-size: 16px;
    line-height: 130%;
    margin-bottom: 7px;
  }
}

.cart__product-info p {
  margin: 0;
  font-size: 18px;
  line-height: 130%;

  @media (max-width: $mobile) {
    font-size: 16px;
    line-height: 130%;
  }
}

.cart__quantity {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: $mobile) {
    margin-right: -4px;
  }
}

.cart__quantity p {
  margin: 0;
  margin-bottom: 5px;

  font-size: 18px;
  line-height: 130%;

  @media (max-width: $mobile) {
    margin-bottom: 2px;
  }
}

.cart__quantity-button {
  position: relative;

  border: 0;
  background: none;
  appearance: none;
  width: 20px;
  height: 20px;
  margin-bottom: 11px;

  cursor: pointer;

  &::after,
  &::before {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    content: "";
    width: 14px;
    height: 2px;
    background: $grey;

    transform: translate(-50%, -50%);
  }

  &--plus {
    &::after {
      transform: translate(-50%, -50%) rotate(90deg);
    }
  }

  &--minus {
    &::before {
      content: none;
    }
  }

  @include hover-default;

  @media (max-width: $mobile) {
    margin-bottom: 10px;
  }
}

.cart__footer {
  width: 100%;
  padding: 15px 35px;

  border-top: 1px solid $neutral;

  @media (max-width: $mobile) {
    padding: 0 15px;
    margin-left: -15px;
    margin-right: -15px;
    width: calc(100% + 30px);
  }
}

.cart__output {
  display: flex;
  justify-content: space-between;
  margin-bottom: 37px;

  @media (max-width: $mobile) {
    flex-direction: column;
    justify-content: flex-start;
    margin-bottom: 17px;
  }
}

.cart__output p {
  margin: 0;
  display: flex;
  justify-content: space-between;

  font-family: $font-family;
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  text-align: center;

  @media (max-width: $mobile) {
    margin-bottom: 15px;
    font-size: 12px;
  }
}

.cart__price p {
  font-weight: 500;

  @media (max-width: $mobile) {
    font-size: 12px;
  }
}

.cart__buttons {
  display: flex;
  justify-content: space-between;

  @media (max-width: $mobile) {
    flex-direction: column;
  }
}

.cart__button {
  flex-basis: 200px;
  max-height: 68px;

  margin-bottom: 20px;

  @include hover-default;
}
